// Type definitions for the car dealership application

export interface Car {
    id: string;
    make: string;
    model: string;
    year: number;
    price: number;
    images: string[];
    description: string;
    mileage: number;
    transmission: 'automatic' | 'manual';
    fuelType: 'petrol' | 'diesel' | 'electric' | 'hybrid';

    // Basic Data
    bodyType: string;          // e.g., "Sedan"
    vehicleCondition: string;  // e.g., "Used"
    driveType: string;         // e.g., "Front-wheel drive"
    seats: number;
    doors: number;
    offerNumber: string;

    // Technical Data
    power: {
        kw: number;
        hp: number;
    };
    engineSize: number;        // in cmÂ³
    gears: number;
    cylinders: number;
    weight: number;            // in kg

    // Environmental Data
    emissionClass: string;     // e.g., "Euro 5"
    fuelConsumption: number;   // l/100km combined
    co2Emissions: number;      // g/km combined

    // Equipment
    comfort: string[];         // e.g., ["Automatic climate control", "Leather interior", ...]
    entertainment: string[];   // e.g., ["Car radio", "Bluetooth", ...]
    safety: string[];         // e.g., ["ABS", "Driver airbag", ...]
    extras: string[];         // e.g., ["Alloy wheels", "Puncture repair kit"]

    // Color and Interior
    color: string;
    specificColor: string;
    paintType: string;
    interiorColor: string;
    upholstery: string;       // e.g., "Full leather"
}

export interface ContactRequest {
    carId: string;
    email?: string;
    phone?: string;
    note: string;
    timestamp: Date;
}